@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Lobster+Two:ital@0;1&display=swap');

* {
    overflow: hidden;
}

body {
    margin: 0px;
    padding: 0px;
}

.weatherAppDivBackgroundImage::after {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    height: 100vh;
    width: 100%;
    backdrop-filter: blur(5px);
}

.weatherAppDivBackgroundImage {
    position: relative;
    background-image: url(../images/china.jpg);
    background-repeat: no-repeat;
    background-position: 0 0 0 100px;
    background-size: cover;
    background-attachment: fixed;
    height: 100vh;
}

.weatherAppHeader {
    text-align: center;
    margin: 0px;
    margin-bottom: 0px;
    padding: 0px;
    font-family: 'Lobster';
}


.weatherAppSearch {
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    top: 5%;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
}

.weatherAppSearchBar {
    width: 450px;
    height: 40px;
    padding: 0px;
    padding-left: 20px;
    font-size: inherit;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    border: 2px solid rgba(0, 0, 0, 0.333);
    border-right: none;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.weatherAppSearchBar::placeholder {
    color: rgb(181, 181, 181);
}

.weatherAppSearchBar:focus {
    background-color: rgba(0, 0, 0, 0.491);
    border: 2px solid rgba(0, 0, 0, 0.282);
    border-right: 0px;
    outline: none;
}

.weatherAppSearchButton {
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    border: 2px solid rgba(0, 0, 0, 0.333);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 50px;
    font-size: 14px;
}

.weatherAppSearchButton:hover {
    background-color: rgba(0, 0, 0, 0.491);
    border: 2px solid rgba(0, 0, 0, 0.282);
    color: white;
}

/*RESULT =================================================================================================================== */

.weatherAppResult {
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    text-align: center;
    top: 450px;
}

.weatherName {
    font-family: "Lobster Two";
    font-style: italic;
    font-size: 70px;
    font-weight: 500;
    margin: 0px;
}

.weatherAppHr {
    width: 200px;
    position: relative;
    top: -20px;
    border: 2px solid black;
}

.locationContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    height: 150px;
}

.locationContainer * {
    margin: 0px;
}

.locationContainer div {
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    align-items: center;
}

.locationTemperature,
.locationWindSpeed,
.locationWeather {
    font-size: 50px;
}


/*RESULT =================================================================================================================== */

.weatherApp404 {
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    text-align: center;
    top: 560px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    padding: 25px;
    font-size: 40px;
}

